@import '_vars';

#posts-news{background-color:#eee;}
#posts-editorial{background-color:#f0f0f0;}

// #home-top{padding-top:1.5rem;}
#hfront-top{padding-top:5rem;}

/*
    stack
*/
.stack{
  &-wrapper,&-section,&-row{display:flex;flex-flow:column wrap;}
  &-wrapper,&-row{
    @media (min-width: $screen-m){flex-flow:row nowrap;}
  }
  //
  // style name: hero
  //
  &.hero &-wrapper{
    border-bottom:1px solid $bdc-m;
  }
  &.hero &-section{
    flex:1 1;
    // top-level: main, side
    &.main{
      min-width:100%;
      @media (min-width: $screen-m){min-width:auto;}
    }
    &.side{
      display:none;
      @media (min-width: $screen-m){display:block;max-width:25%;border-right:1px solid $bdc-m;}
    }
  }
  &.hero &-row{
    border-bottom:1px solid $bdc-m;
    &:last-child,&.mod{border-bottom:0;}
  }
  &.hero &-col{
    flex:1 1;border-right:0;
    @media (min-width: $screen-m){border-right:1px solid $bdc-m;}
    &.fluid,&.right{
      @media (min-width: $screen-m){border-right:0;}
    }
    // sub-level (of main): left, right
    &.left,&.right{
      min-width:100%;
      @media (min-width: $screen-m){min-width:auto;}
    }
    &.left{
      @media (min-width: $screen-m){max-width:60%;}
    }
    &.right{
      @media (min-width: $screen-m){max-width:40%;}
    }
    //
    // mix
    &-head,&-body{
      padding-left:2rem;padding-right:2rem;
      @media (min-width: $screen-m){padding-left:3.5rem;padding-right:3.5rem;}
    }
    //
    // head
    &-head{
      padding-top:0.8rem;padding-bottom:0.6rem;border-bottom:1px solid $bdc-m;
      @media (min-width: $screen-m){padding-top:1.2rem;padding-bottom:1rem;}
      &-name{
        color:$dagreen;font-size:1.4rem;font-weight:400;font-family:'Noto Sans', sans-serif;text-transform:uppercase;text-align:center;letter-spacing:-0.1px;
        @media (min-width: $screen-m){font-size:1.5rem;text-align:left;}
        & span{font-weight:bold;}
      }
    }
    //
    // body
    &-body{
      display:flex;flex-flow:column wrap;padding-top:1rem;padding-bottom:1rem;
      @media (min-width: $screen-m){padding-top:2rem;padding-bottom:2rem;}
      & .article{
        flex:1 1;
      }
      // quick
      &.quick .article{
        margin-bottom:1em;
        &__category{
          font-size:1.15rem;letter-spacing:0;
        }
        &__title{
          margin:0.3em 0;font-size:1.4rem;line-height:1.2;
          @media (min-width: $screen-m){font-size:1.6rem;}
        }
        &__excerpt{
          font-size:1.3rem;line-height:1.2;
        }
      }
      // headlines
      &.headlines-1{
        .article{
          &__category{
            font-size:1.4rem;
            @media (min-width: $screen-m){font-size:1.6rem;}
          }
          &__title{line-height:1.3;}
          &__image{margin:1.5rem 0;}
        }
        & .stack-item:first-child .article{
          width:100%;margin-bottom:0.5rem;padding-bottom:0.5rem;border-bottom:1px solid $bdc-m;text-align:center;
          @media (min-width: $screen-m){margin-bottom:1rem;padding-bottom:1rem;}
          &__title,&__excerpt{line-height:1.3;}
          &__title{
            // font-family:Georgia, serif;
            max-width:80%;margin:0.5rem auto;font-size:2.6rem;
            @media (min-width: $screen-m){font-size:3.4rem;}
          }
          &__excerpt{
            font-size:1.6rem;
            @media (min-width: $screen-m){font-size:1.8rem;}
          }
          &__image{height:40rem;}
        }
        & .stack-item:nth-child(2) .article{

        }
      }
      &.headlines-2{
        .article{
          margin-bottom:1rem;padding-bottom:1rem;border-bottom:1px solid $bdc-m;
          @media (min-width: $screen-m){margin-bottom:2rem;padding-bottom:2rem;}
          &__image{
            height:15rem;margin-bottom:1rem;
            @media (min-width: $screen-m){height:20rem;}
          }
          &__title{margin:0.5rem 0;}
        }
      }
    }
  }
  &.hero &-block{
    margin-bottom:1rem;
    @media (min-width: $screen-m){margin-bottom:2rem;}
  }
}

// home front
.hfstack{
  &-alpha{font-size:0;}
  &-alpha &-col{
    display:inline-block;vertical-align:top;
    &.left{width:80%;padding-right:10px;}
    &.right{width:20%;padding-left:5px;}
  }
  &-alpha &-articles{
    display:inline-block;vertical-align:top;
    & .article{
      padding:18px 0;border-bottom:1px solid #ddd;
      &:first-child{padding-top:0;}
      &:last-child{padding-bottom:0;border-bottom:0;}
      &__image{height:250px;}
      &__category{position:absolute;padding:12px 16px 4px 12px;background-color:$bgc-m;font-size:12px;transform:translateY(-100%);}
      .no-image .article__category{position:relative;transform:none;}
      &__title,&__excerpt{padding:8px 10px 0;}
      &.textonly .article__category{position:relative;padding:6px 10px 2px;transform:none;}
      &.textonly .article__title{padding-top:0;}
    }
  }
  &-alpha &-articles-alpha{
    width:60%;padding-right:15px;border-right:1px solid #ddd;
    & .article{
      &:first-child .article__image{height:300px;}
      &__title{font-size:26px;}
    }
  }
  &-alpha &-articles-beta{
    width:40%;padding-left:15px;
  }
  &-item{
    padding-bottom:10px;
    &:last-child{padding-bottom:0;}
  }
}
.hfpadding{
  padding:20px 0;
  &.xs{padding:10px 0;}
  &.sm{padding:15px 0;}
  &.lg{padding:25px 0;}
}
// preformat
.hfprefmt{
  padding:25px 0;
  &.beta{padding:20px 0;}
}
.hfdivider{display:block;border-top:1px solid #ddd;}
.hfsection{
  &-header{
    padding-bottom:20px;text-align:center;
    // &.beta{text-align:left;}
  }
  &-title{@extend %font-article;color:$punch;font-size:26px;font-weight:bolder;}
  &-beta,&-charlie,&-articles{font-size:0;}
  &-alpha &-articles{
    & .article{
      display:inline-block;width:25%;padding:0 4px;vertical-align:top;
    }
    & .article:nth-child(1){padding-left:0;padding-right:8px;}
    & .article:nth-child(2){padding-right:6px;}
    & .article:nth-child(3){padding-left:6px;}
    & .article:nth-child(4){padding-left:8px;padding-right:0;}
  }
  &-alpha &-articles-alpha .article{
    &__image{height:200px;width:100%;}
    &__content{padding:8px 3px 5px;}
    &__title{line-height:1.2;}
    &__excerpt{padding-top:5px;}
    &__category{display:none;}
  }
  &-beta &-articles{
    &-alpha,&-beta{display:inline-block;vertical-align:top;}
    &-alpha{width:40%;}
    &-beta{
      width:60%;padding-left:20px;
      &.switch{padding-left:0;padding-right:20px;}
    }
  }
  &-beta &-articles-alpha .article{
    position:relative;
    &__image{position:absolute;height:200px;width:100%;max-width:350px;top:-100px;left:50%;border:1px solid #f8f9fa;transform:translateX(-50%);}
    &__content{margin-top:100px;padding:120px 20px 25px;background-color:#f2f2f2;z-index:2;}
    &__category,&__title{text-align:center;}
    &__category{padding-bottom:5px;}
    &__excerpt{max-width:400px;margin:0 auto;padding-top:10px;}
  }
  &-beta &-articles-beta .article{
    padding-top:15px;padding-bottom:15px;border-bottom:1px solid #ddd;
    &:first-child{padding-top:0;}
    &:last-child{padding-bottom:0;border-bottom:0;}
    &__image{display:none;}
    &__content{padding:0 10px;}
    &__category{padding-bottom:5px;color:$carmine-pink;}
    &__excerpt{padding:10px 5px 0 0;}
  }
  &-charlie &-articles-alpha .article{
    display:inline-block;position:relative;width:33.333333%;padding:0 10px;vertical-align:top;
    &__image{
      position:relative;height:300px;
      &::before{
        content:'';position:absolute;height:170px;bottom:0;right:0;left:0;
        background-image: linear-gradient(to top, rgba(30, 30, 30, 190), rgba(30, 30, 30, 0));
      }
      &::after{content:'';display:block;height:9999px;}
    }
    &__content{position:absolute;bottom:0;left:0;right:0;padding:10px 20px;}
    &__category{color:$carnation;}
    &__title{color:#fff;}
    &__excerpt{display:none;}
  }
}
.hfblock{
}
.hfsubscribe{
  padding:20px 15px;background-color:$dagreen;font-size:15px;
  &-desc{padding-bottom:10px;color:#f2f2f2;}
  &-btn{@extend %font-article;padding:10px;color:#333;background-color:#f2f2f2;font-weight:bold;text-align:center;}
}
